@charset "UTF-8";
.movie-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 30px;
}

.movie-header h1 {
  padding-top: 10px;
  color: #4692ff;
}

.movie-contents {
  padding: 30px 0 50px;
}

.movie-contents .movie-loading {
  color: #fff;
}

.movie-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  gap: 30px;
}

.movie-list li {
  width: 100%;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.movie-list a {
  display: block;
  position: relative;
  padding: 16px;
  border-radius: 16px;
  background: #363636;
  opacity: .8;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  color: #fff;
}

.movie-list a:hover {
  -webkit-transform: translate3d(0, -10px, 0);
          transform: translate3d(0, -10px, 0);
  opacity: 1;
}

.movie-list .image {
  position: relative;
  width: 100%;
  height: 260px;
  background: #222;
  border-radius: 12px;
  overflow: hidden;
}

.movie-list .image::after {
  content: "";
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 100px;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0)), to(rgba(0, 0, 0, 0.8)));
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8));
}

.movie-list .image img {
  position: absolute;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.movie-list h3 {
  z-index: 1;
  position: absolute;
  bottom: 0;
  padding: 10px 12px;
  font-size: 20px;
}

.movie-list .info {
  margin: 10px 6px;
  font-size: 13px;
  color: #bbb;
}

.movie-list .info-genre {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.movie-list .info-genre span + span::before {
  content: "·";
  margin: 0 2px;
}

.movie-list .summary {
  display: -webkit-box;
  height: 88px;
  padding: 0 6px;
  overflow: hidden;
  word-break: break-all;
  text-overflow: ellipsis;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  font-size: 15px;
  line-height: 22px;
}

.movie-list .rating {
  margin: 10px 6px 0;
  font-size: 13px;
}

.movie-list .rating-star {
  display: inline-block;
  width: 15px;
  height: 19px;
  margin-right: 2px;
}

.movie-list .rating-star svg {
  width: 100%;
  height: 19px;
  vertical-align: top;
}

.movie-detail {
  position: relative;
  min-height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.movie-detail::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
}

.movie-detail .movie-contents {
  padding: 0;
}

.movie-detail .movie-detail-contents {
  z-index: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  position: relative;
  margin: 0 auto;
  padding: 30px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.movie-detail .detail-poster {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 133.33333%;
  border-radius: 16px;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.4);
}

.movie-detail .detail-poster img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.movie-detail .detail-text {
  padding: 50px 0;
}

.movie-detail .title {
  font-size: 42px;
  line-height: 1.2;
  color: #fff;
}

.movie-detail .subText {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-top: 10px;
  color: #fff;
}

.movie-detail .genre {
  color: #bbb;
}

.movie-detail .genre span + span::before {
  content: "·";
  margin: 0 2px;
}

.movie-detail .year {
  margin-left: 12px;
}

.movie-detail .rating {
  margin-left: 12px;
}

.movie-detail .rating-star {
  display: inline-block;
  width: 15px;
  height: 26px;
  margin-right: 4px;
}

.movie-detail .rating-star svg {
  width: 100%;
  height: 26px;
  vertical-align: top;
}

.movie-detail .description {
  margin-top: 60px;
  font-size: 16px;
  line-height: 1.5;
  color: #fff;
}

@media screen and (min-width: 600px) {
  .movie-list li {
    width: calc(50% - 15px);
  }
}

@media screen and (min-width: 860px) {
  .movie-list li {
    width: calc(33.3333% - 20px);
  }
  .movie-detail .movie-detail-contents {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    max-width: 800px;
    height: 100vh;
    padding: 0 30px;
  }
  .movie-detail .detail-poster {
    -ms-flex-negative: 0;
        flex-shrink: 0;
    width: 300px;
    padding-top: 450px;
  }
  .movie-detail .detail-text {
    margin-left: 50px;
  }
}
